input{
    width: 100%;
      font-size: 1em;
      padding: 0 0.62em;
      border: none;
      border-bottom: 2px solid #3d64e6;
      outline: none;
      color: #222a43;
  }
  
  .heading {
      color: #fff;
      margin: 30px;
      font-weight: 600;
    }
    .line{
      border:none;
      width: 100%;
    
        margin-top: 0;
        margin-bottom: 20px;
        border-bottom: 1px solid #5f5f5f;
        box-shadow: 0 20px 20px -20px #333;
    
    }
  
    
    img {max-width: 100%;}
            .inbox_msg {
              border: 1px solid #c4c4c4;
              clear: both;
              overflow: hidden;
            }
            .top_spac{ margin: 20px 0 0;}
    
    
            .recent_heading {float: left; width:40%;}
    
            .headind_srch{ padding:10px 29px 10px 20px; overflow:hidden; border-bottom:1px solid #c4c4c4;}
    
            .recent_heading h4 {
              color: #05728f;
              font-size: 21px;
              margin: auto;
            }
    
            .chat_ib h5{ font-size:15px; color:#464646; margin:0 0 8px 0;}
            .chat_ib h5 span{ font-size:13px; float:right;}
            .chat_ib p{ font-size:14px; color:#989898; margin:auto}
            .chat_img {
              float: left;
              width: 11%;
            }
            .chat_ib {
              float: left;
              padding: 0 0 0 15px;
              width: 88%;
            }
    
            .chat_people{ overflow:hidden; clear:both;}
            .chat_list {
              border-bottom: 1px solid #c4c4c4;
              margin: 0;
              padding: 18px 16px 10px;
            }
            .inbox_chat { height: 550px; overflow-y: scroll;}
    
            .active_chat{ background:#ebebeb;}
    
            .incoming_msg_img {
              display: inline-block;
              width: 6%;
            }
            .received_msg {
              display: inline-block;
              padding: 0 0 0 10px;
              vertical-align: top;
              width: 92%;
             }
             .received_withd_msg p {
              background: #e4e8fb none repeat scroll 0 0;
              border-radius: 3px;
              color: #646464;
              font-size: 14px;
              margin: 0;
              padding: 5px 10px 5px 12px;
              width: 100%;
            }
            .time_date {
              color: #747474;
              display: block;
              font-size: 10px;
              margin: 3px 0 0;
            }
            .received_withd_msg { width: 70%;}
            .mesgs {
              float: left;
              padding: 40px;
            }
    
             .sent_msg p {
              background: #3F51B5 none repeat scroll 0 0;
              border-radius: 3px;
              font-size: 14px;
              margin: 0; color:#fff;
              padding: 5px 10px 5px 12px;
              width:100%;
            }
            .outgoing_msg{ overflow:hidden; margin:26px 0 26px;}
            .sent_msg {
          float: right;
              width: 70%;
          text-align: left;
            }
            .input_msg_write input {
              background: rgba(0, 0, 0, 0) none repeat scroll 0 0;
              border: medium none;
              color: #4c4c4c;
              font-size: 15px;
              min-height: 48px;
              width: 100%;
            }
    
            .type_msg {border-top: 1px solid #c4c4c4;position: relative;}
            .msg_send_btn {
              background: #05728f none repeat scroll 0 0;
              border: medium none;
              border-radius: 50%;
              color: #fff;
              cursor: pointer;
              font-size: 17px;
              height: 33px;
              position: absolute;
              right: 0;
              top: 11px;
              width: 33px;
            }
            .messaging { background: #fff;}
            .msg_history {
              max-height: 516px;
              overflow-y: auto;
            }
  